.div1 {
  float: left;
  width: 32%;
  height: 100%;
}
.div1 .titleNode {
  color: #fff;
  width: 384px;
  height: 120px;
  /* float: left; */
  line-height: 120px;
  text-align: center;
  cursor: pointer;
  position: relative;
  /* margin-left: 88px; */
  font-size: 44px;
  text-shadow: 0 0 30px #f4faf5;
}
.div1 .realtime {
  margin-top: 29px;
  width: 788px;
  height: 723px;
}
.container {
  overflow: hidden;
  width: 730px;
  top: 190px;
  position: relative;
  left: 17px;
  height: 520px;
}
.labeltextleft {
  position: absolute;
  width: 100px;
  color: #C6E5FF;
  left: 266px;
  top: 540px;
  font-size: 26px;
  fill: #C6E5FF;
}
.labeltextright {
  position: absolute;
  width: 100px;
  color: #C6E5FF;
  right: 250px;
  top: 590px;
  font-size: 26px;
  fill: #C6E5FF;
}
.righttbox {
  position: relative;
  top: -594px;
  left: 430px;
  width: 315px;
  height: 219px;
  z-index: 99999;
}
.righttbox .xian {
  float: left;
  display: inline-block;
  width: 70px;
  height: 219px;
}
.righttbox .text {
  float: left;
  margin-left: 10px;
  margin-top: 15px;
  display: inline-block;
  width: 221px;
  height: 219px;
}
.righttbox .text span {
  width: 221px;
  height: 44px;
  font-size: 31px;
  text-align: center;
  line-height: 44px;
  letter-spacing: 2px;
  color: #ffffff;
  background-image: linear-gradient(0deg, #2668dc 0%, #7979f4 100%), linear-gradient(#0090ff, #0090ff);
  background-blend-mode: normal, 
        normal;
  border-radius: 6px;
}
.righttbox .text p {
  font-size: 40px;
  letter-spacing: 2px;
  color: #ffffff;
  font-weight: 700;
}
.righttbox .text p i {
  font-style: normal;
  font-size: 30px;
  letter-spacing: 2px;
  color: #ffffff;
  font-weight: 400;
}
.leftbox {
